<script>
//  vue-axios import axios from 'axios'
import Sign from '../components/Sign.vue'
import Login from '../components/Login.vue'
export default {
  name: 'App',
  data: () => {
    return {
      // filter: '',
      // drawer: null,
      // admins: [
      //   ['Management', 'mdi-account-multiple-outline'],
      //   ['Settings', 'mdi-cog-outline']
      // ],
    }
  },
  components: {
    Sign,
    Login
  },
  computed: {
    user() {
      return this.$store.state.user
    }
  },
  methods: {
    logout() {
      this.axios
        .delete(process.env.VUE_APP_API + '/users/logout')
        .then(res => {
          // 
          if (res.data.success) {
            this.$swal({
              icon: 'success',
              title: '',
              text: ''
            })

            //  vuex
            this.$store.commit('logout')

            // 
            if (this.$route.path !== '/') {
              this.$router.push('/')
            }
          } else {
            this.$swal({
              icon: 'error',
              title: '',
              text: res.data.message
            })
          }
        })
        .catch(error => {
          //  200 alert 
          this.$swal({
            icon: 'error',
            title: '',
            text: error.response.data.message
          })
        })
    },
    heartbeat() {
      this.axios
        .get(process.env.VUE_APP_API + '/users/heartbeat')
        .then(res => {
          //  vuex 
          if (this.user.id.length > 0) {
            // 
            if (!res.data) {
              this.$swal({
                icon: 'error',
                title: '',
                text: ''
              })
              // 
              this.$store.commit('logout')
              // 
              if (this.$route.path !== '/') {
                this.$router.push('/')
              }
            }
          }
        })
        .catch(() => {
          this.$swal({
            icon: 'error',
            title: '',
            text: ''
          })
          // 
          this.$store.commit('logout')
          // 
          if (this.$route.path !== '/') {
            this.$router.push('/')
          }
        })
    }
  },
  mounted() {
    this.heartbeat()
    setInterval(() => {
      this.heartbeat()
    }, 5000)
  }
}
</script>
