<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawer" permanent color="blue-grey lighten-5" app>
      <v-sheet color="blue-grey lighten-4" class="pa-4">
        <v-avatar class="mb-4" color="white" size="64">
          <img :src="'./images/penking-logo-yellow.png'" alt="John" />
        </v-avatar>
        <p></p>
      </v-sheet>

      <v-divider></v-divider>
      <!-- ------------------------------- -->
      <v-list v-for="item in items" :key="item.path">
        <v-list-group v-if="item.children" v-model="item.active" :prepend-icon="item.action" :index="item.path" no-action>
          <template v-slot:activator>
            <router-link class="navnoline" :to="item.path">
              <v-list-item-content>
                <v-list-item-title v-text="item.title"> </v-list-item-title>
              </v-list-item-content>
            </router-link>
          </template>

          <router-link class="navnoline" v-for="(citem, cindex) in item.children" :to="citem.path" :key="cindex">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title v-text="citem.title"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </router-link>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click="logout">
        <v-icon>mdi-logout-variant</v-icon>
      </v-btn>
      <router-link class="navnoline" to="/">
        <v-btn icon>
          <v-icon>mdi-home</v-icon>
        </v-btn>
      </router-link>
    </v-app-bar>
    <!--  -->
    <router-view></router-view>
  </v-app>
</template>

<script>
export default {
  name: 'head-nav',
  computed: {
    user() {
      return this.$store.getters.user
    }
  },
  methods: {
    logout() {
      this.axios
        .delete(process.env.VUE_APP_API + '/users/logout')
        .then(res => {
          // 
          if (res.data.success) {
            this.$swal({
              icon: 'success',
              title: '',
              text: ''
            })

            //  vuex
            this.$store.commit('logout')

            // 
            if (this.$route.path !== '/') {
              this.$router.push('/')
            }
          } else {
            this.$swal({
              icon: 'error',
              title: '',
              text: res.data.message
            })
          }
        })
        .catch(error => {
          //  200 alert 
          this.$swal({
            icon: 'error',
            title: '',
            text: error.response.data.message
          })
        })
    },
    heartbeat() {
      this.axios
        .get(process.env.VUE_APP_API + '/users/heartbeat')
        .then(res => {
          //  vuex 
          if (this.user.id.length > 0) {
            // 
            if (!res.data) {
              this.$swal({
                icon: 'error',
                title: '',
                text: ''
              })
              // 
              this.$store.commit('logout')
              // 
              if (this.$route.path !== '/') {
                this.$router.push('/')
              }
            }
          }
        })
        .catch(() => {
          this.$swal({
            icon: 'error',
            title: '',
            text: ''
          })
          // 
          this.$store.commit('logout')
          // 
          if (this.$route.path !== '/') {
            this.$router.push('/')
          }
        })
    }
  },
  data: () => ({
    cards: ['Today', 'Yesterday'],
    drawer: null,
    // links: [
    //   ['mdi-inbox-arrow-down', ''],
    //   ['mdi-send', ''],
    //   ['mdi-delete', 'Trash'],
    //   ['mdi-alert-octagon', 'Spam']
    // ],
    items: [
      {
        action: 'mdi-briefcase',
        children: [{ path: 'push', title: '' }],
        title: '',
        path: 'Back2'
      },
      {
        action: 'mdi-account-supervisor',
        active: true,
        children: [{ path: 'UserList', title: '' }],
        title: '',
        path: 'UserList'
      },
      {
        action: 'mdi-clipboard-text',
        children: [{ path: '/OrderForm', title: '' }],
        title: '',
        path: 'CheckOrder'
      },
      {
        action: 'mdi-chat-processing',
        children: [{ path: '/ConnectionList', title: '' }],
        title: '',
        path: 'ConnectionList'
      }
    ]
  })
}
</script>

